.routes {
  padding: 100px 0;
  position: relative; }
  @media (max-width: 767px) {
    .routes {
      padding: 30px 0 50px; } }
  .routes .swiper-pagination {
    bottom: 30px; }
    @media (max-width: 767px) {
      .routes .swiper-pagination {
        bottom: 20px; } }
  .routes .swiper-pagination-bullet {
    background-color: #759CDC; }
  .routes .swiper-pagination-bullet-active {
    background-color: #1A3B70; }

.routes__slider {
  overflow: hidden; }

.routes__item {
  display: grid;
  grid-template-columns: 60% 40%;
  min-height: 300px;
  margin-left: 1px; }

.routes__item-description {
  margin-top: 30px;
  margin-bottom: 30px;
  padding: 20px 0 20px 20px;
  border-top: 1px solid #1A3B70;
  border-left: 1px solid #1A3B70;
  border-bottom: 1px solid #1A3B70;
  border-top-left-radius: 10px;
  border-bottom-left-radius: 10px;
  padding-right: 15px; }

.routes__item-description p {
  color: var(--divider, #8592B5);
  font-family: Open Sans;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 130%;
  margin: 0 0 15px 0; }

.routes__item-description h3 {
  margin: 0 0 20px 0; }

.routes__item-picture img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 10px;
  margin-right: 2px; }

.services {
  padding: 100px 0;
  position: relative; }
  @media (max-width: 767px) {
    .services {
      padding: 30px 0; } }

.services__wrapper {
  display: grid;
  grid-template-columns: 31.5% 1fr;
  gap: 50px; }
  @media (max-width: 1023px) {
    .services__wrapper {
      grid-template-columns: 1fr; } }

.services__table {
  padding: 20px;
  border-radius: 10px;
  background-color: #759CDC;
  color: #ffffff; }

.services__row {
  display: grid;
  grid-template-columns: 30% 15% 48%;
  gap: 25px;
  padding-bottom: 15px;
  border-bottom: 1px solid #ffffff;
  margin-bottom: 20px; }
  @media (max-width: 519px) {
    .services__row {
      grid-template-columns: 1fr; } }

.services__row:first-child {
  text-transform: uppercase; }
  @media (max-width: 519px) {
    .services__row:first-child {
      display: none; } }

.services__item p {
  font-size: 14px;
  margin: 0; }

.services__picture {
  max-height: 600px;
  position: relative; }
  @media (max-width: 1023px) {
    .services__picture {
      display: none; } }

.services__picture::before {
  content: '';
  position: absolute;
  bottom: -20px;
  right: -25px;
  width: 80%;
  height: 100%;
  border-radius: 10px;
  border: 2px solid #1A3B70;
  z-index: -1; }
  @media (max-width: 1023px) {
    .services__picture::before {
      display: none; } }

.map {
  min-height: 800px;
  background-image: url("../img/content/map.jpg");
  background-position: center; }

.popup {
  opacity: 0;
  visibility: hidden;
  position: fixed;
  width: 100%;
  height: 100%;
  background-color: rgba(3, 16, 27, 0.7);
  top: 0;
  left: 0;
  z-index: 70;
  overflow-y: auto;
  overflow-x: hidden;
  transition: opacity 0.3s ease; }
  .popup.is-active {
    opacity: 1;
    visibility: visible; }

.popup__body {
  width: 100%;
  min-height: 100%;
  padding: 30px 10px;
  display: flex;
  align-items: center;
  justify-content: center; }

.popup__content {
  position: relative;
  box-sizing: border-box;
  min-width: 630px;
  min-height: 430px;
  padding: 20px;
  background-color: #ffffff;
  border-radius: 25px;
  text-align: center; }
  @media (max-width: 767px) {
    .popup__content {
      min-width: 100%;
      padding-top: 40px; } }

.popup__title {
  font-size: 33px;
  font-style: normal;
  font-weight: 700;
  line-height: 44px; }
  @media (max-width: 767px) {
    .popup__title {
      font-size: 20px;
      line-height: 1.2; } }

.popup__text {
  font-size: 16px;
  font-weight: 400;
  line-height: 23px; }

.popup__btn {
  position: absolute;
  width: 64px;
  height: 64px;
  top: 10px;
  right: 10px;
  cursor: pointer;
  border: none;
  padding: 10px;
  border-radius: 50%;
  transition: background-color 0.3s ease;
  background-color: transparent; }
  @media (max-width: 767px) {
    .popup__btn {
      top: 5px;
      right: 5px; } }

.popup__btn:hover {
  background-color: rgba(0, 0, 0, 0.04); }
